.footer {
    z-index: 100;
    height: 100%;
    margin-bottom: 0;
}

footer {
    font-size: 14px;
    position: fixed;
    bottom: 0;
    left: calc(50% - 107.42px)
}

ul.Social-Icons {
    display: flex;
    width: 100vw;
    justify-content: center;
    list-style-type: none;
    padding: 16px;
    margin: 0;
}


.icon {
    border-radius: 50%;
    padding: 8px;
    margin: 16px 8px;
    transition: all .2s ease-in-out;
}

:hover.icon {
    cursor: pointer;
    transform: scale(1.2);
}

.tooltiptext {
    visibility: hidden;
    font-family: Poppins;
    position: absolute;
    font-size: 12px;
    padding: 4px 8px;
    border-radius: 10%;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
    display: inline-block;
    position:absolute;
    bottom: -105%;
    left:50%;
    transform:translateX(-50%);
    text-align: center;
    background-color: white;
}